@import '~antd/dist/antd.css';
#root {
  width: 100%;
  height: 100%;
}
.page-container {
  width: 100%;
  height: 100%;
}
.page-container .ant-layout-has-sider {
  height: 100%;
}
.page-container .ant-layout-sider {
  background-color: #fff;
}
.page-container .ant-layout-sider header {
  height: 64px;
  background-color: #00ff48;
  text-align: center;
  line-height: 64px;
}
.page-container .ant-layout-sider .menu-container {
  height: calc(100% - 64px);
}
.page-container .ant-layout-header {
  background-color: aqua;
  text-align: center;
}
.page-container .ant-layout-content {
  padding: 16px;
}
.page-container .ant-layout-content .content-container {
  width: 100%;
  height: 100%;
  padding: 16px;
  background-color: #fff;
}
